<template>
  <div id="collection-detail-component" v-if="detail">
    <div class="top-container">
      <div class="top-wrapper">
        <div class="title-container">
          <h1 class="title">{{ detail.name }}</h1>
          <h2 class="info">{{ detail.description }} {{ hashtags }}</h2>
        </div>
        <div>
          <div class="buttons-container">
            <el-button
              v-permission="'project_add'"
              type="success"
              @click="onStartProjectClick"
              >Initiate Project</el-button
            >
            <el-button
              v-permission="'collection_update'"
              type="info"
              @click="onEditClick"
            >
              Edit
            </el-button>
            <el-button
              v-permission="'collection_analysis_view'"
              type="info"
              @click="onAnalysisClick"
            >
              Analysis
            </el-button>
            <div v-permission="'collection_download'">
              <el-tooltip
                v-model="downloadVisible"
                placement="bottom"
                effect="light"
                :disabled="!downloadVisible"
                popper-class="download-console"
              >
                <template #content>
                  <div class="download-button" @click="onDownloadExcel">
                    <img :src="excelDownloadIcon" alt="excel download icon" />
                  </div>
                  <div class="download-button" @click="onDownloadPpt(1)">
                    <img :src="pptDownloadIcon" alt="ppt download icon" />
                    <p>Basic</p>
                  </div>
                  <div class="download-button" @click="onDownloadPpt(2)">
                    <img :src="pptDownloadIcon" alt="ppt download icon" />
                    <p>2022</p>
                  </div>
                </template>
                <el-button
                  type="info"
                  @click="downloadVisible = !downloadVisible"
                >
                  <font-awesome-icon
                    class="icon"
                    :icon="['fas', 'download']"
                  ></font-awesome-icon>
                </el-button>
              </el-tooltip>
            </div>
          </div>
          <h2 class="info" :style="{ marginLeft: '15px' }">
            Created by: {{ detail.owner_username }}
          </h2>
        </div>
      </div>
    </div>

    <div class="cards-container">
      <el-row :gutter="60">
        <el-col
          v-for="(item, index) in detail.collection_fabrics"
          :key="index"
          :span="8"
        >
          <FabricCardEditable
            :data="item"
            @edit="onCardEditClick"
            @add="onCardAddClick"
            @delete="onCardDeleteConfirm"
          ></FabricCardEditable>
        </el-col>
      </el-row>
    </div>

    <CreateProjectDialog ref="createProjectDialog"></CreateProjectDialog>
    <CreateDialog editable ref="editDialog" @load="onEditLoad"></CreateDialog>

    <!--For Fabric Card Editable Component-->
    <AddToCollectionDialog ref="addToCollectionDialog"></AddToCollectionDialog>

    <el-dialog
      :title="`Edit Price: ${editData.name}`"
      v-model="dialogCardEditVisible"
    >
      <div class="edit-main">
        <div class="input-item">
          <p class="label">Price:</p>
          <el-input v-model="editData.price" type="number" clearable></el-input>
        </div>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" @click="dialogCardEditVisible = false">
            Cancel
          </el-button>
          <el-button type="success" @click="onEditConfirm">
            Save
          </el-button>
        </span>
      </template>
    </el-dialog>

    <WantToSeeMore
      v-permission="'contact_us'"
      :id="parseInt(id, 10)"
      category="collection"
    ></WantToSeeMore>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  ref,
  reactive,
  toRefs,
  computed,
  onMounted,
  Ref
} from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

import { loading } from "@/utils";
import FabricCardEditable from "@/components/FabricCardEditable.vue";
import CreateDialog from "./CreateDialog.vue";
import CreateProjectDialog from "./CreateProjectDialog.vue";
import AddToCollectionDialog from "@/components/AddToCollectionDialog.vue";
import WantToSeeMore from "@/components/WantToSeeMore.vue";
import excelDownloadIcon from "@/assets/images/icons/excel_download_icon.svg";
import pptDownloadIcon from "@/assets/images/icons/ppt_download_icon.svg";

import {
  DeleteCollectionFabric,
  GetCollection,
  PostCollectionUpdateFabricPrice
} from "@/typings/store";
import {
  CollectionAddFabric,
  CollectionFabrics,
  CollectionUpdateFabricPriceParams
} from "@/typings/collection";
import { ElNotification } from "element-plus";
import { iCreateDialog } from "@/typings/component";
import {
  postCollectionUpdateFabricPrice,
  postUpdateCollection,
  getDownloadPpt
} from "@/apis/collection";

const Component = defineComponent({
  name: "CollectionDetail",
  components: {
    FabricCardEditable,
    CreateProjectDialog,
    CreateDialog,
    AddToCollectionDialog,
    WantToSeeMore
  },
  props: {
    id: {
      type: String,
      default: ""
    }
  },
  setup(props) {
    const router = useRouter();
    const store = useStore();
    const createProjectDialog: Ref<iCreateDialog | null> = ref(null);
    const addToCollectionDialog: Ref<iCreateDialog | null> = ref(null);
    const editDialog: Ref<iCreateDialog | null> = ref(null);

    const { id } = toRefs(props);
    const dialogCardEditVisible = ref(false);
    const downloadVisible = ref(false);
    const editData = reactive({
      id: 0,
      name: "",
      price: 0
    });

    const detail = computed(() => store.state.collection.detail);
    const hashtags = computed(() => {
      let str = "";
      if (detail.value.year) str += `#${detail.value.year}, `;
      if (detail.value.season) str += `#${detail.value.season}, `;
      if (detail.value.hash_tag) {
        str += detail.value.hash_tag
          .split(",")
          .map((it: string) => `#${it}`)
          .join(",");
      }
      return str;
    });

    const init = async () => {
      const loadingInstance = loading();
      try {
        await store.dispatch(GetCollection, id.value);
      } catch (_) {
        await router.replace({ name: "Error" });
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(init);

    const onStartProjectClick = () => {
      if (!createProjectDialog.value) return;
      createProjectDialog.value.open(id.value);
    };
    const onEditClick = async () => {
      if (!editDialog.value) return;
      const params = {
        name: detail.value.name,
        description: detail.value.description,
        year: detail.value.year,
        season: detail.value.season,
        show_label: detail.value.show_label,
        theme: detail.value.theme,
        hash_tag: detail.value.hash_tag,
        status: detail.value.status,
        // TODO remove creator when api fixed.
        owner: 4,
        creator: 4,
        sport_type: []
      };
      await postUpdateCollection(params, parseInt(id.value, 10));
      editDialog.value.open(detail.value);
    };

    const onAnalysisClick = async () => {
      await router.push({
        name: "CollectionAnalysis",
        params: { id: id.value }
      });
    };
    const onCardEditClick = async (data: CollectionFabrics) => {
      const params: CollectionUpdateFabricPriceParams = {
        collection_id: id.value,
        fabric_id: data.fabric.id,
        price: data.price
      };
      await postCollectionUpdateFabricPrice(params);

      editData.id = data.fabric.id;
      editData.name = data.fabric.name;
      editData.price = data.price;
      dialogCardEditVisible.value = true;
    };
    const onCardAddClick = (data: CollectionAddFabric) => {
      if (!addToCollectionDialog.value) return;
      addToCollectionDialog.value.open(data);
    };

    const onCardDeleteConfirm = async (data: CollectionFabrics) => {
      const params: CollectionUpdateFabricPriceParams = {
        collection_id: id.value,
        fabric_id: data.fabric.id
      };
      await store.dispatch(DeleteCollectionFabric, params);
      await init();
    };

    const onEditConfirm = async () => {
      const params: CollectionUpdateFabricPriceParams = {
        collection_id: id.value,
        fabric_id: editData.id,
        price: editData.price
      };
      try {
        await store.dispatch(PostCollectionUpdateFabricPrice, params);
        dialogCardEditVisible.value = false;
        ElNotification({
          title: "Success",
          type: "success",
          customClass: "message",
          message: `Edit fabric ${editData.name} price to $${editData.price} successfully.`
        });
      } finally {
        await init();
      }
    };

    const onEditLoad = async () => {
      await init();
    };

    const onDownloadExcel = async () => {
      console.log("work");
    };

    const onDownloadPpt = async (templateId: number) => {
      const loadingInstance = loading();
      try {
        const { data, headers } = await getDownloadPpt(
          parseInt(id.value, 10),
          templateId
        );
        const url = URL.createObjectURL(data);
        const a = document.createElement("a");
        a.download = headers["content-disposition"]
          .split("=")[1]
          .replaceAll('"', "");
        a.href = url;
        a.click();
        setTimeout(() => URL.revokeObjectURL(url), 5000);
      } finally {
        loadingInstance.close();
      }
    };

    return {
      createProjectDialog,
      addToCollectionDialog,
      editDialog,

      dialogCardEditVisible,
      downloadVisible,
      editData,
      detail,
      hashtags,

      onStartProjectClick,
      onEditClick,
      onAnalysisClick,

      onCardEditClick,
      onCardAddClick,
      onCardDeleteConfirm,
      onEditConfirm,
      onEditLoad,
      onDownloadExcel,
      onDownloadPpt,

      excelDownloadIcon,
      pptDownloadIcon
    };
  }
});

export default Component;
</script>

